"달마시안",
"아프간하운드",
"그레이하운드",
"이탈리안그레이하운드",
"휘핏",
"아키타",
"시바견",
"진돗개",
"풍산개",
"차우차우",
"불테리어",
"폭스테리어",
"잭러셀테리어",
"파슨러셀테리어",
"스코티시테리어",
"웨스트하이랜드화이트테리어",
"아이리시테리어",
"케언테리어",
"노퍽테리어",
"노리치테리어",
"베들링턴테리어",
"실리엄테리어",
"에어데일테리어",
"와이어폭스테리어",
"소프트코티드휘튼테리어",
"티베탄테리어",
"라사압소",
"페키니즈",
"차이니즈크레스티드",
"시체드 (시추+비숑)",
"코통드툴레아르",
"하바니즈",
"파피용",
"필라브라질레이로",
"카네코르소",
"보르도마스티프",
"티베탄마스티프",
"프레사카나리오",
"도고아르헨티노",
"벨지안말리노이즈",
"벨지안테뷰런",
"벨지안쉬펜도스",
"벨지안라케노이즈",
"올드잉글리시쉽독",
"셰틀랜드쉽독",
"버니즈마운틴독",
"그레이트피레니즈",
"레온베르거",
"아이리시세터",
"고든세터",
"잉글리시세터",
"브리타니스패니얼",
"아이리시워터스패니얼",
"스프링어스패니얼",
"클럼버스패니얼",
